.main-button {
  padding: clamp(21px, 1.9231vw, 25px) clamp(42px, 4.6154vw, 60px);
  background-color: rgba(var(--bg-a), 1);
  border: 1px solid rgba(var(--cl-f), 1);
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  font-family: var(--font-second);
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 0.02em;
  color: rgba(var(--cl-f), 1);
  text-align: center;
  transition: all 0.3s ease-in-out;
}
.main-button:hover {
  color: rgba(var(--bg-a), 1);
  background-color: rgba(var(--bg-f), 1);
}

.details {
  display: flex;
  align-items: center;
  gap: 10px;
}
.details li {
  display: flex;
  align-items: center;
  gap: 5px;
}
.details li p {
  font-size: 14px;
  line-height: 129%;
}
.details li svg {
  fill: transparent;
}

.articles {
  width: 100%;
}
.articles__items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: clamp(16px, 3.125vw, 32px);
}
@media (max-width: 1024px) {
  .articles__items {
    grid-template-columns: repeat(2, 1fr);
  }
}
.articles__item {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.articles__top {
  position: relative;
}
.articles__image {
  display: flex;
}
.articles__image picture,
.articles__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 16px;
}
.articles__tags {
  position: absolute;
  left: 10px;
  bottom: 10px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 4px;
}
.articles__tags li {
  padding: 2px 6px 4px 6px;
  background: rgba(var(--bg-b), 0.4);
  border-radius: 4px;
  font-family: var(--font-primary);
  font-size: 14px;
  line-height: 114%;
  font-weight: 700;
  letter-spacing: -0.02em;
  color: rgba(var(--cl-a), 1);
}
.articles__info {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.articles__title {
  font-family: var(--font-primary);
  font-size: clamp(20px, 1.667vw, 24px);
  line-height: 133%;
  font-weight: 700;
  letter-spacing: -0.02em;
}
.articles__more {
  margin: clamp(24px, 5.469vw, 56px) auto 0;
  padding: 14px 56px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: clamp(16px, 1.758vw, 18px);
  line-height: 167%;
  font-weight: 500;
  letter-spacing: -0.02em;
  color: rgba(var(--cl-d), 1);
  background: rgba(var(--bg-a), 1);
  border: 2px solid rgba(var(--cl-d), 1);
  border-radius: 100px;
}
.articles__more svg {
  stroke: currentColor;
}

.quiz {
  position: relative;
  z-index: 2;
  display: grid;
  grid-template-columns: 1fr 218px;
}
.quiz::before {
  content: "";
  position: absolute;
  background-image: var(--quiz-frame-bg);
  background-repeat: no-repeat;
  inset: 0;
  z-index: -1;
}
.quiz__info {
  padding: 32px 24px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.quiz__duration {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 14px;
  line-height: 129%;
  letter-spacing: -0.02em;
}
.quiz__title {
  font-family: var(--font-primary);
  font-size: 24px;
  line-height: 133%;
  font-weight: 700;
  letter-spacing: -0.02em;
}
.quiz__btn {
  width: fit-content;
  padding: 12px 16px 12px 20px;
  display: flex;
  align-items: center;
  gap: 10px;
  background: linear-gradient(315deg, #0a8bd3 0%, #2ab2ff 100%);
  border-radius: 100px;
  font-size: 16px;
  line-height: 150%;
  font-weight: 500;
  letter-spacing: -0.02em;
  color: rgba(var(--cl-a), 1);
}
.quiz__image {
  position: relative;
  display: flex;
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 12px;
}
.pagination__list {
  display: flex;
  align-items: center;
  grid-gap: clamp(5px, 0.868vw, 10px);
}
.pagination__list-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  border: 2px solid transparent;
  border-radius: 100px;
  font-size: 16px;
  line-height: 125%;
  font-weight: 500;
  letter-spacing: -0.02em;
  transition: all 0.4s ease-in-out;
  cursor: pointer;
}
.pagination__list-link:hover, .pagination__list-link.active, .pagination__list-link:active {
  color: rgba(var(--cl-d), 1);
  border: 2px solid rgba(var(--bg-d), 1);
}
.pagination__nav {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  transition: all 0.3s ease;
}
.pagination__nav svg {
  stroke: currentColor;
}
.pagination__nav--prev {
  transform: scale(-1);
}
.pagination__nav.inactive {
  color: rgba(var(--cl-c), 0.25);
  pointer-events: none;
}
.pagination__nav:hover, .pagination__nav.active, .pagination__nav:active {
  color: rgba(var(--cl-d), 1);
}

.feedback {
  position: relative;
  padding: 38.5px 62px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.feedback::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: var(--feedback-odd-frame);
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1;
}
.feedback__info {
  display: flex;
  align-items: center;
  gap: 16px;
}
.feedback__avatar {
  display: flex;
}
.feedback__author {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.feedback__author span {
  font-family: var(--font-primary);
  font-size: 18px;
  line-height: 133%;
  font-weight: 700;
  color: rgba(var(--cl-d), 1);
}
.feedback__author p {
  font-family: var(--font-primary);
  font-size: 18px;
  line-height: 133%;
  font-weight: 400;
}
.feedback__text {
  font-size: 18px;
  line-height: 167%;
  font-weight: 300;
  font-style: italic;
  letter-spacing: -0.02em;
}
.feedback__link {
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 16px;
  line-height: 150%;
  font-weight: 500;
  color: rgba(var(--cl-d), 1);
  letter-spacing: -0.02em;
  transition: all 0.4s ease;
}
.feedback__link svg {
  transition: all 0.4s ease;
  stroke: currentColor;
}

.main-form {
  border: 1px solid rgba(var(--bg-b), 0.1);
  border-radius: 10px;
  background: rgba(var(--bg-g), 1);
  display: flex;
  align-items: center;
  grid-gap: clamp(9px, 5.7692vw, 75px);
  padding: clamp(32px, 2.6154vw, 34px) clamp(32px, 2.6154vw, 34px) clamp(32px, 4.6154vw, 60px);
}
.main-form__box {
  width: 100%;
  display: flex;
  flex-direction: column;
  grid-gap: clamp(20px, 4.6154vw, 60px);
  text-align: center;
}
@media (max-width: 576px) {
  .main-form {
    flex-direction: column;
    padding-bottom: 0;
    grid-gap: 9px;
  }
}
.main-form__inner {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: clamp(14px, 1.5385vw, 20px);
}
@media (max-width: 1024px) {
  .main-form__inner {
    grid-template-columns: 1fr;
  }
}
.main-form__label {
  width: 100%;
  position: relative;
}
.main-form__input {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 24px clamp(20px, 1.9231vw, 25px);
  border: 1px solid rgba(var(--cl-f), 0.4);
  background: rgba(var(--bg-a), 1);
  border-radius: 10px;
  font-family: var(--font-second);
  font-size: 15px;
  line-height: 130%;
  color: rgba(var(--cl-c), 1);
}
.main-form__input::placeholder {
  color: rgba(var(--cl-c), 0.5);
}
.main-form .title {
  text-transform: uppercase;
}
.main-form__list {
  display: flex;
  flex-direction: column;
  grid-gap: 16px;
}
.main-form__row {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: clamp(16px, 1.5385vw, 20px);
}
.main-form__row--mode {
  grid-template-columns: 1fr;
}
@media (max-width: 576px) {
  .main-form__row {
    grid-template-columns: 1fr;
  }
}
.main-form__area {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 24px clamp(20px, 1.9231vw, 25px);
  border: 1px solid rgba(var(--cl-f), 0.4);
  background: rgba(var(--bg-a), 1);
  border-radius: 10px;
  font-family: var(--font-second);
  font-size: 15px;
  line-height: 130%;
  color: rgba(var(--cl-c), 1);
  min-height: clamp(120px, 11.0769vw, 144px);
}
.main-form__area::placeholder {
  color: rgba(var(--cl-c), 0.5);
}

.main-nav {
  display: flex;
  align-items: center;
}
.main-nav__link {
  padding: 10px 12px;
  line-height: 125%;
  border-radius: 40px;
  background: transparent;
  transition: all 0.4s ease;
}
.main-nav__link:hover {
  background: rgba(var(--bg-c), 0.04);
}
.main-nav__link:active {
  background: rgba(var(--bg-c), 0.08);
}
@media (max-width: 1024px) {
  .main-nav {
    display: none;
  }
}

header {
  padding-top: 16px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 80;
}
@media (max-width: 1024px) {
  header {
    padding-top: 0;
  }
  header .container {
    padding: 0;
  }
}

.header {
  position: relative;
  z-index: 30;
  display: grid;
  grid-template-columns: repeat(2, auto) 1fr;
  align-items: center;
  grid-gap: clamp(10px, 1.29vw, 16px);
  padding: 12px;
  background-color: rgba(var(--bg-a), 1);
  border-radius: 72px;
  box-shadow: 0 32px 64px 0 rgba(50, 50, 71, 0.04), 0 16px 32px 0 rgba(50, 50, 71, 0.04);
}
@media (max-width: 1024px) {
  .header {
    border-radius: 0;
    gap: 0;
    width: fit-content;
    margin-left: auto;
    padding: 20px 30px;
  }
}
.header__logo {
  display: flex;
}
.header__logo svg {
  width: clamp(155px, 15.806vw, 196px);
}
@media (min-width: 1024px) {
  .header__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
@media (max-width: 1024px) {
  .header__logo {
    display: none;
  }
}
.header__btns {
  display: flex;
  align-items: center;
  gap: 12px;
}
@media (min-width: 1024px) {
  .header__btns {
    justify-self: end;
  }
}
@media (max-width: 1024px) {
  .header__btns {
    display: none;
  }
}
.header__boarded {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 14px 20px;
  border: 2px solid rgba(var(--cl-f), 1);
  background: transparent;
  border-radius: 100px;
  font-size: 16px;
  line-height: 125%;
