.quiz-section__frame {
  position: absolute;
  top: -140px;
  right: 103px;
  display: flex;
}
.quiz-section__text {
  font-family: var(--font-primary);
  font-size: 26px;
  line-height: 154%;
  font-weight: 600;
  letter-spacing: -0.02em;
}
.quiz-section__variants {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 32px;
}
@media (max-width: 1024px) {
  .quiz-section__variants {
    grid-template-columns: 100%;
  }
}
.quiz-section__more {
  margin: 0 auto;
  padding: 14px 56px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 18px;
  line-height: 167%;
  font-weight: 500;
  letter-spacing: -0.02em;
  color: rgba(var(--cl-e), 1);
  background: linear-gradient(180deg, #fff 0%, #fafafa 100%);
  border-radius: 100px;
}
.quiz-section__more svg {
  stroke: currentColor;
}

.feedbacks-section {
  position: relative;
  padding: 60px 0;
  overflow: hidden;
}
.feedbacks-section::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background-image: var(--feedbacks-bg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 491px;
}
.feedbacks-section__top {
  margin-bottom: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.feedbacks-section__slider {
  width: 100%;
  height: 100%;
}
.feedbacks-section__progressbar {
  margin: 60px auto 0;
  max-width: 490px;
  width: 100%;
  height: 32px;
  border-radius: 450px;
  display: flex;
  align-items: center;
  padding: 8px;
  background: #e8e0d6;
}
.feedbacks-section__pagination {
  position: relative;
  padding: 8px;
  border-radius: 20px;
  background-color: #dbd3c9;
}
.feedbacks-section__pagination .swiper-pagination-progressbar-fill {
  height: 16px;
  border-radius: 50px;
  background-color: #fff;
}

.info-section {
  padding: clamp(40px, 6.944vw, 100px) 0 clamp(80px, 6.944vw, 100px);
}
.info-section__logo {
  display: flex;
  justify-content: center;
  margin-bottom: clamp(32px, 4.444vw, 64px);
}
.info-section__logo svg {
  width: clamp(196px, 20.417vw, 294px);
  height: clamp(40px, 4.167vw, 60px);
}
.info-section__wrapp {
  margin: 0 auto;
  max-width: 1100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  text-align: center;
}
.info-section__heading {
  font-family: var(--font-primary);
  font-size: 20px;
  line-height: 160%;
  font-weight: 700;
  color: rgba(var(--cl-d), 1);
}
.info-section__btn {
  padding: 14px 24px;
  display: flex;
  align-items: center;
  gap: 10px;
  background-color: rgba(var(--bg-d), 1);
  border-radius: 100px;
  font-family: var(--font-second);
  font-size: 18px;
  line-height: 167%;
  font-weight: 500;
  letter-spacing: -0.02em;
  color: rgba(var(--cl-a), 1);
}

.filters-section {
  position: relative;
  padding: 60px 0;
}
.filters-section__wrapp {
  display: flex;
  flex-direction: column;
  gap: clamp(16px, 2.222vw, 32px);
}
.filters-section__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}
.filters-section__link {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 16px;
  line-height: 150%;
  font-weight: 500;
  letter-spacing: -0.02em;
  color: rgba(var(--cl-d), 1);
}
.filters-section__link svg {
  stroke: currentColor;
}
@media (max-width: 767px) {
  .filters-section__link {
    display: none;
  }
}
.filters-section__controls {
  margin-bottom: 32px;
}
.filters-section__items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: clamp(16px, 2.222vw, 32px);
}
@media (max-width: 1024px) {
  .filters-section__items {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 576px) {
  .filters-section__items {
    grid-template-columns: 100%;
  }
}
.filters-section__item {
  position: relative;
  padding: 24px;
  height: fit-content;
}
.filters-section__item::before {
  content: "";
  position: absolute;
  background-image: var(--material-frame-bg);
  background-repeat: no-repeat;
  background-size: cover;
  inset: 0;
  z-index: -1;
}
.filters-section__item-image {
  display: flex;
  margin-bottom: 24px;
}
.filters-section__item-image picture,
.filters-section__item-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}
.filters-section__item-title {
  display: block;
  margin-bottom: 8px;
  font-family: var(--font-primary);
  font-size: clamp(20px, 1.667vw, 24px);
  line-height: 133%;
  font-weight: 700;
}
.filters-section__item-link {
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-left: auto;
  color: rgba(var(--cl-d), 1);
}
.filters-section__item-link svg {
  stroke: currentColor;
}
.filters-section__pagination {
  margin-top: 32px;
}

.subscribe-section {
  position: relative;
  padding: 60px 0;
}
.subscribe-section__wrapp {
  width: 100%;
  padding: 48px;
  display: flex;
  align-items: center;
  gap: 48px;
  background: linear-gradient(180deg, #f6e2b7 0%, #fade97 100%);
  border-radius: 64px;
  box-shadow: 16px 30px 76px 0 rgba(0, 0, 0, 0.05), 64px 122px 137px 0 rgba(0, 0, 0, 0.04), 144px 274px 186px 0 rgba(0, 0, 0, 0.03), 256px 486px 220px 0 rgba(0, 0, 0, 0.01), 400px 760px 240px 0 rgba(0, 0, 0, 0);
}
@media (max-width: 992px) {
  .subscribe-section__wrapp {
    flex-direction: column;
  }
}
.subscribe-section__image {
  display: flex;
  max-width: 454px;
  width: 100%;
  flex-shrink: 0;
  transform: skew(-3deg);
}
.subscribe-section__image picture,
.subscribe-section__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 48px;
}
.subscribe-section__info {
  max-width: 660px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.subscribe-section__heading {
  font-family: var(--font-primary);
  font-size: clamp(24px, 1.806vw, 26px);
  line-height: 154%;
  font-weight: 600;
}
.subscribe-section__heading .brown {
  color: #d36c40;
}
.subscribe-section__heading .green {
  color: #0d7768;
}
.subscribe-section__text {
  font-size: clamp(14px, 1.111vw, 16px);
  font-weight: 300;
  line-height: 169%;
  letter-spacing: -0.02em;
}
.subscribe-section__text a {
  font-weight: 400;
  text-decoration: underline;
  text-decoration-skip-ink: none;
}
.subscribe-section__form {
  padding: 16px 0;
}
.subscribe-section__form label {
  height: 54px;
  width: 100%;
  position: relative;
  border-radius: 100px;
  padding: 14px 24px;
  background: rgba(var(--bg-a), 1);
  box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.12);
}
.subscribe-section__form input {
  font-family: var(--font-second);
  font-size: 18px;
  line-height: 167%;
  letter-spacing: -0.02em;
  color: rgba(var(--cl-c), 1);
}
.subscribe-section__form input::placeholder {
  color: rgba(var(--cl-c), 0.75);
}
.subscribe-section__form button {
  position: absolute;
  height: 50px;
  top: 50%;
  right: 4px;
  transform: translateY(-50%);
  padding: 10px 16px;
  display: flex;
  align-items: center;
  gap: 10px;
  background: linear-gradient(180deg, #a5a5a5 0%, #707070 100%);
  border-radius: 100px;
  color: rgba(var(--cl-a), 1);
}