.first-section{
    position: relative;
    padding: clamp(30px, 5.859vw, 60px) 0;
    z-index: 2;

    &__wrapp {
        display: flex;
        flex-direction: column;
        gap: clamp(60px, 5.556vw, 80px)
    }

    &__title {
        max-width: 720px;

        @include laptop {
            max-width: 100%;
        }
    }

    &__articles {
        display: grid;
        grid-template-columns: 1fr 41%;
        align-items: start;
        grid-gap: 40px clamp(40px, 5.729vw, 110px);

        @include tablet {
            grid-template-columns: repeat(2, 1fr);
            align-items: stretch;
        }

        @include small-tablet {
            grid-template-columns: 100%;
        }
    }

    &__article {
        position: relative;
        display: grid;
        grid-template-columns: 1fr 188px;

        &::before {
            content: '';
            position: absolute;
            left: 0;
            background-image: var(--first-aside-bg);
            background-size: cover;
            background-repeat: no-repeat;
            width: 100%;
            height: 100%;
            z-index: -1;
        }

        &-image {
            position: relative;
            display: flex;
            order: 1;
            transform: skew(-1deg);

            picture,
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 18px;
                box-shadow: 0 24px 48px 0 rgba(50, 50, 71, 0.06), 0 12px 24px 0 rgba(50, 50, 71, 0.04);
            }
        }

        &-info {
            padding: 0 clamp(16px, 2.222vw, 32px);
            display: flex;
            align-items: center;
        }

        &-heading {
            font-family: var(--font-primary);
            font-size: clamp(20px, 1.667vw, 24px);
            line-height: 133%;
            font-weight: 700;
            letter-spacing: -0.02em;
        }

        &-text {
            display: none;
            margin-bottom: clamp(4px, 0.833vw, 12px);
            font-size: clamp(16px, 1.389vw, 20px);
            line-height: 160%;
            font-weight: 300;
        }

        &-details {
            display: none;
        }

        &:first-child {
            grid-row: span 3;
            display: grid;
            grid-template-columns: 100%;

            .first-section__article-image {
                order: -1;
                margin-bottom: 24px;

                picture,
                img {
                    box-shadow: 0 32px 64px 0 rgba(50, 50, 71, 0.04), 0 16px 32px 0 rgba(50, 50, 71, 0.04);
                    border-radius: 61px;
                }
            }

            .first-section__article-info {
                padding: 0 16px;
                flex-direction: column;
                align-items: flex-start;
            }

            .first-section__article-heading {
                display: block;
                margin-bottom: 4px;
                font-family: var(--font-primary);
                font-size: clamp(24px, 2.5vw, 36px);
                line-height: 133%;
                font-weight: 600;
            }

            .first-section__article-text {
                display: flex;
            }

            .first-section__article-details {
                display: flex;
            }     
            
            &::before {
                display: none;
            }
        }

        @include tablet {
            display: flex;
            flex-direction: column;

            &::before {
                display: none;
            }

            &:first-child {
                grid-row: initial;
                display: flex;
                flex-direction: column;

                .first-section__article-image {
                    flex-grow: 1;
                }
            }
            
            &-image {
                order: -1;
                margin-bottom: 24px;

                picture,
                img {
                    box-shadow: 0 32px 64px 0 rgba(50, 50, 71, 0.04), 0 16px 32px 0 rgba(50, 50, 71, 0.04);
                    border-radius: 61px;
                }
            }

            &-info {
                padding: 0 16px;
                flex-direction: column;
                align-items: flex-start;
                flex-grow: 1;
            }

            &-heading {
                display: block;
                margin-bottom: 4px;
                font-family: var(--font-primary);
                font-size: clamp(24px, 2.5vw, 36px);
                line-height: 133%;
                font-weight: 600;
            }

            &-text {
                display: flex;
                flex-grow: 1;
            }

            &-details {
                display: flex;
            }
        }
    }
}